.header{                  
  width: 100%;
  top: 0;
  left: 0;
  background-color: var(--container-color);
  z-index: var(--z-fixed);
}

.Navbar {
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
  background: whitesmoke;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: black;
  font-weight: var(--font-medium);
}

a {
  color: #141619;
  text-decoration: none;
  --bs-link-hover-color: silver;
  font-weight: 600;
}

.Navbar {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: whitesmoke;
}

.Navbar>.nav-logo {
  font-weight: 700;
  font-size: 21px;
  margin: 15px;
  color: black;
}

.Navbar>.nav-items>a {
  color: black;
  font-size: 16px;
  text-decoration: none;
  margin: 15px;
  position: relative;
  opacity: 0.9;
}

.Navbar>.nav-items>a:hover {
  opacity: 1;
}

.Navbar>.nav-items>a::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: black;
  transition: all 0.45s;
}

.Navbar>.nav-items>a:hover::before {
  width: 100%;
}

.Navbar>.nav-toggle {
  display: none;
}

@media (max-width: 700px) {
  .Navbar>.nav-items {
    position: absolute;
    top: 60px;
    display: flex;
    flex-direction: column;
    background: whitesmoke;
    left: 0;
    width: 30%;
    height: 31%;
    transform: translateX(-100%);
    transition: all .45s;
  }

  .Navbar>.nav-items>a::before {
    background: transparent;
  }

  .Navbar>.nav-items.open {
    transform: translateX(0);
  }

  .Navbar>.nav-toggle {
    display: flex;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  .nav-toggle>.bar {
    position: relative;
    width: 32px;
    height: 2px;
    background: black;
    transition: all 0.45s ease-in-out;
  }

  .nav-toggle>.bar::before,
  .nav-toggle>.bar::after {
    content: "";
    position: absolute;
    height: 2px;
    background: black;
    border-radius: 2px;
    transition: all 0.45s ease-in-out;
  }

  .nav-toggle>.bar::before {
    width: 25px;
    transform: translateY(-8px);
    right: 0;
  }

  .nav-toggle>.bar::after {
    width: 32px;
    transform: translateY(8px);
  }

  .nav-toggle.open>.bar {
    transform: translateX(-40px);
    background: transparent;
  }

  .nav-toggle.open>.bar::before {
    width: 32px;
    transform: rotate(45deg) translate(26px, -26px);
  }

  .nav-toggle.open>.bar::after {
    transform: rotate(-45deg) translate(26px, 26px);
  }
  a {
    font-size: medium;
  }
}
